'use client';

import { ColumnDef } from '@tanstack/react-table';

import { ArrowUpDown, MoreHorizontal } from 'lucide-react';

import { Button } from '@/components/ui/button';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';

// Define the types for the data structure
export type KeyStatus = 'available' | 'borrowed' | 'returned' | 'lost';

export type BorrowedKey = {
  borrowerName: string; // Name of the person
  company?: string; // Company name if applicable
  email: string; // Contact email of the borrower
  phone: string; // Optional phone number
  keyId: string; // Key ID
  status: KeyStatus; // Status of the key
  borrowedAt: string; // Date when the key was borrowed
  returnedAt?: string; // Date when the key was returned (optional)
};

// Define the columns for the table
export const columns: ColumnDef<BorrowedKey>[] = [
  {
    accessorKey: 'borrowerName',
    header: ({ column }) => {
      return (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === 'asc')}
          className="p-0 text-left"
        >
          Name
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      );
    },
  },
  {
    accessorKey: 'keyId',
    header: 'Key',
  },
  {
    accessorKey: 'status',
    header: 'Status',
  },
  {
    accessorKey: 'date',
    header: 'Last change',
    cell: ({ row }) => {
      // Determine the correct date based on the status
      const status = row.original.status;
      const date = status === 'returned' ? row.original.returnedAt : row.original.borrowedAt;

      return <div>{date}</div>;
    },
  },
  {
    id: 'actions',
    cell: ({ row }) => {
      const BorrowedKey = row.original;

      return (
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" className="h-8 w-8 p-0">
              <span className="sr-only">Open menu</span>
              <MoreHorizontal className="h-4 w-4" />
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            <DropdownMenuLabel>Actions</DropdownMenuLabel>
            <DropdownMenuItem onClick={() => navigator.clipboard.writeText(BorrowedKey.phone)}>
              Call {BorrowedKey.phone}
            </DropdownMenuItem>
            <DropdownMenuSeparator />
            <DropdownMenuItem>Return key</DropdownMenuItem>
            <DropdownMenuItem>View key access</DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      );
    },
  },
];
